.header {
  width: 100%;
  height: 6rem;
  padding: 0 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #252424;
  box-shadow: 0 40px 160px rgba(177, 170, 170, 0.2),
    0 1px 1px rgba(226, 219, 219, 0.2),
    0 2px 2px rgba(231, 217, 217, 0.2),
    0 4px 4px rgba(167, 154, 154, 0.2),
    0 8px 8px rgba(0, 0, 0, 0.2);
}

.navLink {
  display: flex;
}

.navLink-link {
  color: #1ad1b9;
  text-decoration: none;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 12px;
}

.navLink-dropdown {
  position: absolute;
  opacity: 0;
  /* visibility: hidden; */
  transition: opacity 0.8s ease-in-out;
  z-index: -1;
  top: 60px;
  left: 0px;
  /* transition: opacity 1s ease-in-out; */
}

.navLink-item:hover .navLink-dropdown {
  opacity: 1;
  visibility: visible;
  z-index: 1;
}

.navLink-dropdown-item {
  border: 1px solid #1ad1b9;
  width: 200%;
  padding: 5px;
  font-size: 9px;
  /* padding: 5px 50px; */
  text-decoration: none;
  display: block;
  color: #1ad1b9;
  background-color: #270841;
}

.navLink-dropdown-item:hover {
  background-color: rgb(124, 127, 129);
  transition: background-color 0.4s ease-in-out;
  opacity: 1;
}

.navLink-item {
  padding: 25px;
  cursor: pointer;
  position: relative;
}

.navLink-item:hover {
  background-color: rgb(124, 127, 129);
  transition: background-color 0.8s ease-in-out;
}

.navLink-item-btn {
  padding: 25px;
}

.logo img {
  margin-top: 10px;
}

.logo-credo {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  padding: 10px 0px;
  font-size: 7px;
  position: relative;
  text-align: center;
  margin-top: 7px;
  letter-spacing: 3px;
}

.logo-credo:before,
.logo-credo:after {
  content: "";
  position: absolute;
  width: 20%;
  height: 2px;
  background-color: white;
  top: 55%;
  transform: translateY(-50%);
}

.logo-credo:before {
  left: 5px;
}

.logo-credo:after {
  right: 20px;
}

.header h1 {
  color: white;
}

.header ul {
  list-style: none;
  margin: 0;
  padding: 0;
}